:root {
  --highlight: thistle;
}



/*GENERAL*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  
}

body {
  height: 100vh;
  width: 100vw;
  background-color: rgb(255, 255, 255);
}

#root {
  height: 100%;
  max-width: 500px;
  max-height: 800px;
  margin: 0 auto;
  background-color: white;
  border: solid black 4px;
  border-radius: 15px;
}
.App{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
/*NAVBAR*/
.navbarWrapper {
  position: relative;
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
}
.navbar {
  background-color: rgb(255, 255, 255);
  height: 100%;
  display: flex;
  border-bottom: solid rgb(194, 194, 194) 2px;
}
.navbarContent {
  position: absolute;
  top: 50px;
  background-color: rgb(168, 244, 250);
  display: none;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 100%;
  transition: height 1s;
}
.navbarContent * {
  color: black;
  width: 100%;
  font-size: 28px;
  padding: 15px;
}
.navbarContent *:hover{
  background-color: var(--highlight);;
}
#hamburger {
  background-color: white;
  height: 100%;
  min-width: 50px;
  display: flex;
  flex-direction: column;
  justify-content:space-evenly;
  align-items: center;
  padding: 10px 0;
}
#hamburger:hover {
  background-color: thistle;
}

#hamburger:hover .navbarContent {
  display: flex;
}


#hamburger div {
  background-color: black;
  height: 2px;
  width: 80%;
}
#pageTitle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  padding-right: 25px;
}

.showNavigation {
  display: flex;
}

/*VIEW*/
.view {
  background-color: thistle;
}